const _ = require("lodash");
const FastestValidator = require("fastest-validator");
const ClassHelper = require("./share-code.js").ClassHelper;

//// ParamUtil
class ParamUtil {
  // ParamUtil.is_valid(schema_key, data_obj)
  static is_valid(schema_key, data_obj) {
    const schema_obj = ParamUtil[schema_key];
    const validator_obj = new FastestValidator();
    const check_obj = validator_obj.compile(schema_obj);
    return check_obj(data_obj);
  }

  static init = {
    player_item_col: { type: "object" },
    controler_obj: { type: "object" },
  };

  static add_item = {
    player_item_col: { type: "object" },
    controler_obj: { type: "object" },
    el_id: { type: "string" },
  };
}

class FormaterItemAttr {

    // FormaterItemAttr.to_item_obj(param_obj)
    static to_item_obj(param_obj={}) {
        const result_obj = _.pick(param_obj.attr, ['is_equal_match'])
        result_obj['start_timestamp'] = new Date(Date.now()).toLocaleString()
        result_obj['team_item_list'] = FormaterItemAttr.to_team_item_list(param_obj.attr.team_item_col)
        return result_obj
        // TODO
    }

    // FormaterItemAttr.to_team_item_list(team_item_col)
    static to_team_item_list(team_item_col) {
        const team_item_list = []

        for(let item of team_item_col.attr.main_item_list) {
            const team_item = {team_score:0}
            team_item.player_item_list = item.attr.player_item_list
            team_item_list.push(team_item)
        }

        console.log(team_item_list.length)
        return team_item_list
        // TODO
    }
}


