class MatchPlayItem {
  constructor(param_obj = {}) {
    MatchPlayItemHelper.init(this, param_obj);
  }

  start(param_obj = {}) {
    MatchPlayItemHelper.start(this, param_obj);
  }
}

class MatchPlayItemCol {
  constructor(param_obj = {}) {
    MatchPlayItemColHelper.init(this, param_obj);
  }

  add_item(param_obj = {}) {
    MatchPlayItemColHelper.add_item(this, param_obj);
  }

  add_item_with_attr(param_obj = {}) {
    MatchPlayItemColHelper.add_item_with_attr(this, param_obj);
  }

  get_main_item_list() {
    return this.attr.main_item_list
  }
}

