//// Robin Gen
class RobinDoubleNumGen {
  // RobinDoubleNumGen.gen_number()
  static gen_number(player_count) {
    let match_list = [];
    if (0 == player_count % 2) {
      match_list = RobinDoubleNumGen._gen_even(player_count);
    } else {
      match_list = RobinDoubleNumGen._gen_odd(player_count);
    }
    return match_list;
  }

  static _gen_even(player_count) {
    let match_list = pairsCalc(player_count);
    if (player_count > 4) {
      const two_d_list = pairsCalc(player_count);
      match_list = RobinDoubleNumGen._gen_match_list(two_d_list);
    }

    return match_list;
  }

  static _gen_odd(player_count) {
    const initial_list = pairsCalc(player_count);

    const two_d_list = initial_list.map((row) => {
      return _.drop(row, 1);
    });

    let match_list = two_d_list;
    if (player_count > 5) {
      match_list = RobinDoubleNumGen._gen_match_list(two_d_list);
    }
    return match_list;
  }

  // RobinDoubleNumGen._gen_match_list(two_d_list)
  static _gen_match_list(two_d_list) {
    const extra_last_index = two_d_list - 2;
    const flat_list = _.flatten(two_d_list);

    if (need_extra_pair(flat_list)) {
      const extra_index = _.random(0, extra_last_index);
      const extra_pair = flat_list[extra_index];
      flat_list.push(extra_pair);
    }
    const match_list = _.chunk(flat_list, 2);

    return match_list;

    function need_extra_pair(flat_list) {
      let result = true;
      result = result && flat_list.length > 4;
      result = result && 0 != flat_list.length % 2;
      return result;
    }
  }
}

class RobinDoubleGenHelper {
  // RobinDoubleGenHelper.gen_match_item_list(param_obj)
  static gen_match_item_list(param_obj = {}) {
    const match_item_col = RobinDoubleGenHelper.gen_match_item_col(param_obj);
    const item_list = match_item_col.attr.main_item_list;
    // const item_list = match_item_col.to_js_obj()
    // const item_list = match_item_col.attr.main_item_list;

    return item_list;
  }

  // RobinDoubleGenHelper._gen_match_item(player_item_list, match_index_row)
  static _gen_match_item(player_item_list, match_index_row) {
    const match_item = new MatchItem();

    for (let robin_index_list of match_index_row) {
      const team_item = RobinDoubleGenHelper.get_match_team_item(
        player_item_list,
        robin_index_list
      );
      match_item.add_team(team_item);
    }
    match_item.set_attr("is_equal_match", match_item._is_equal_match());

    return match_item;
    // TODO
  }

  // RobinDoubleGenHelper.gen_match_item_col(param_obj)
  static gen_match_item_col(param_obj = {}) {
    const player_item_list = param_obj.player_item_list;
    const player_count = player_item_list.length;
    const robin_index_2d_list = RobinDoubleNumGen.gen_number(player_count);

    const match_item_col = new MatchItemCol();
    for (let match_index_row of robin_index_2d_list) {
      const match_item = RobinDoubleGenHelper._gen_match_item(
        player_item_list,
        match_index_row
      );
      match_item_col.add_item(match_item);
    }

    return match_item_col;
  }

  // RobinMatchHelper.get_match_team_item(player_item_list, match_index_row)
  static get_match_team_item(player_item_list, robin_index_list) {
    const team_item = new TeamItem();

    const player_pair_list = RobinDoubleGenHelper.add_team_player(
      team_item,
      player_item_list,
      robin_index_list
    );

    return team_item;
  }

  // RobinMatchHelper.add_team_player(player_item_list, match_index_row)
  static add_team_player(team_item, player_item_list, robin_index_list) {
    let player_pair_list = [];
    robin_index_list.forEach((robin_index) => {
      const player_index = robin_index - 1;
      team_item.add_player(player_item_list[player_index]);
    });
  }
}

class RobinDoubleGen {
  // RobinDoubleGen.gen_match_item_list(param_obj)
  static gen_match_item_list(param_obj = {}) {
    return RobinDoubleGenHelper.gen_match_item_list(param_obj);
  }

  // RobinDoubleGen.gen_match_item_col(param_obj)
  static gen_match_item_col(param_obj = {}) {
    return RobinDoubleGenHelper.gen_match_item_col(param_obj);
  }
}

//// MatchItem
class MatchItemHelper {
  // MatchItemHelper.init(this, param_obj)
  static init(self, param_obj) {
    ClassHelper.init_attr(self, param_obj);
    self.attr.team_item_col = new TeamItemCol();
  }
}

class MatchItemColHelper {
  // MatchItemColHelper.init(this, param_obj)
  static init(self, param_obj) {
    ClassHelper.init_attr(self, param_obj);
    self.attr.main_item_list = [];
  }
}

class MatchItem extends ItemBase {
  constructor(param_obj = {}) {
    super(param_obj);
    MatchItemHelper.init(this, param_obj);
  }

  add_team(team_item) {
    this.attr.team_item_col.add_team(team_item);
  }

  _is_equal_match() {
    const a_rating = this.attr.team_item_col.get_team_item(0).get_play_rating();
    const b_rating = this.attr.team_item_col.get_team_item(1).get_play_rating();

    return a_rating === b_rating;
  }
}

class MatchItemCol extends ItemListBase {
  constructor(param_obj = {}) {
    super(param_obj);
    MatchItemColHelper.init(this, param_obj);
  }

  add_item(match_item) {
    this.attr.main_item_list.push(match_item);
  }
}

class MatchPoolHelper {
  // MatchPoolHelper.init(this, param_obj)
  static init(self, param_obj) {
    ClassHelper.init_attr(self, param_obj);
    self.attr.match_item_list = [];
  }

  // MatchPoolHelper.create_pool_match(this, param_obj)
  static create_pool_match(self, param_obj) {
    const is_valid = ParamUtil.is_valid("create_pool_match", param_obj);

    if (is_valid) {
      const player_item_list = param_obj.player_item_col.filter_by_id_list(
        param_obj.player_id_list
      );
      param_obj.player_item_list = player_item_list;
      const _match_item_list = RobinDoubleGen.gen_match_item_list(param_obj);

      for (let match_item of _match_item_list) {
        self.attr.match_item_list.push(match_item);
      }
    } else {
      console.error("Error MatchPoolHelper.create_pool_match()");
    }

    return self.attr.match_item_list;
    // TODO
  }

  // MatchPoolHelper.create_pool_match_multi(this, param_obj)
  static create_pool_match_multi(self, param_obj) {
    const is_valid = ParamUtil.is_valid("create_pool_match_multi", param_obj);
    if (is_valid) {
      const count = param_obj.count;
      for (let i = 0; i < count; i++) {
        let next_id_list = _.shuffle(_.cloneDeep(param_obj.player_id_list));
        param_obj.player_id_list = next_id_list;
        MatchPoolHelper.create_pool_match(self, param_obj);
      }
    } else {
      console.error("Error MatchPoolHelper.create_pool_match_multi()");
    }
    return self.attr.match_item_list;

    // TODO
  }
}

class MatchPool {
  constructor(param_obj = {}) {
    MatchPoolHelper.init(this, param_obj);
  }

  create_pool_match(param_obj = {}) {
    return MatchPoolHelper.create_pool_match(this, param_obj);
  }

  create_pool_match_multi(param_obj = {}) {
    return MatchPoolHelper.create_pool_match_multi(this, param_obj);
  }
  
  get_match_item_list() {
    const all_match_item_list = FormaterItemAttr.to_match_item_list(
      this.attr.match_item_list
    );
    return all_match_item_list
  }

  get_match_equal_item_list() {
    const all_match_item_list = FormaterItemAttr.to_match_item_list(
      this.attr.match_item_list
    );

    const equal_match_item_list = [];
    for (let item of all_match_item_list) {
      if (true == item.is_equal_match) {
        equal_match_item_list.push(item);
      }
    }

    return equal_match_item_list;
  }
  
  get_match_item_js_list() {
    // TODO
    return this.attr.match_item_list;
  }
}

