const ClassHelper = require("./share-code.js").ClassHelper;
const ItemBase = require("./share-code.js").ItemBase;
const ItemListBase = require("./share-code.js").ItemListBase;



//// RobinMeet
class RobinMeetHelper {
    // RobinMeetHelper.init(this, param_obj)
    static init(self, param_obj) {
      ClassHelper.init_attr(self, param_obj);
      self.attr.play_match_group_list = [];
      self.attr.pool_match_group_list = [];
    }
  
    // RobinMeetHelper.create_pool_match_group(this, param_obj)
    static create_pool_match_group(self, param_obj = {}) {
      const is_valid = ParamUtil.is_valid("create_pool_match_group", param_obj);
  
      if (is_valid) {
        const match_group_obj = new MatchGroup(param_obj);
        match_group_obj.generate_match_group(param_obj);

        let next_id_list = _.shuffle(_.cloneDeep(param_obj.player_id_list));
        param_obj.player_id_list = next_id_list;
        match_group_obj.generate_match_group(param_obj);
        
        const _match_item_list = match_group_obj.get_match_item_list();
        console.log(_match_item_list.length);
        FileUtil.write_json_temp(_match_item_list);
        // TODO
      } else {
        console.error("Error RobinMeetHelper.create_pool_match_group()");
      }
    }
  }
  
  class RobinMeet {
    constructor(param_obj = {}) {
      RobinMeetHelper.init(this, param_obj);
    }
  
    create_play_match_group(param_obj = {}) {}
  
    create_pool_match_group(param_obj = {}) {
      RobinMeetHelper.create_pool_match_group(this, param_obj);
    }
  }

  module.exports = {
    RobinMeet: RobinMeet,
  };
  