const _ = require("lodash");
const FastestValidator = require("fastest-validator");
// const fs = require("fs-extra");
const pairsCalc = require("robin-js");
// const path = require("path");

//// ParamUtil
class ParamUtil {
  // ParamUtil.is_valid(schema_key, data_obj)
  static is_valid(schema_key, data_obj) {
    const schema_obj = ParamUtil[schema_key];
    const validator_obj = new FastestValidator();
    const check_obj = validator_obj.compile(schema_obj);
    return check_obj(data_obj);
  }

  static gen_match_item_col = {
    player_item_list: { type: "array", items: "object" },
  };

  static create_pool_match_group = {
    player_id_list: { type: "array" },
  };
}

//// Util
class ClassHelper {
  // ClassHelper.init_attr(this, param_obj)
  static init_attr(self, param_obj = {}) {
    self.attr = Object.assign({}, param_obj);
  }
}

//// Super Class
class ItemBase {
  constructor(param_obj = {}) {
    ClassHelper.init_attr(this, param_obj);
  }

  get_attr(key) {
    return this.attr[key];
  }

  set_attr(key, value) {
    this.attr[key] = value;
  }

  to_js_obj() {
    return Object.assign({}, this.attr);
  }
}

class ItemListBase {
  constructor(param_obj = {}) {
    ClassHelper.init_attr(this, param_obj);
    // this.attr.main_item_list = [];
  }

  get_attr(key) {
    return this.attr[key];
  }

  set_attr(key, value) {
    this.attr[key] = value;
  }

  to_js_obj() {
    let _item_list = [];
    for (let item of this.attr.main_item_list) {
      _item_list.push(item.to_js_obj());
    }

    return Object.assign({}, this.attr, { main_item_list: _item_list });
  }
}


module.exports = {
    ParamUtil: ParamUtil,
    ClassHelper: ClassHelper,
    ItemBase: ItemBase,
    ItemListBase: ItemListBase
  }