const RobinControl = require('./src/robin-control.js').RobinControl

const cc_id_list = [1, 7, 8, 9, 11, 38, 24, 37]


const control_obj = new RobinControl()

document.addEventListener("DOMContentLoaded", () => {
    const param_obj = {player_id_list:  cc_id_list}
    control_obj.start(param_obj)
  });