//// RobinControl
class RobinControlHelper {
  // RobinControlHelper.init(this, param_obj)
  static init(self, param_obj) {
    ClassHelper.init_attr(self, param_obj);
    self.attr.player_item_col = new PlayerItemCol();

    const player_param = {
      controler_obj: self,
      player_item_col: self.attr.player_item_col,
    };
    self.attr.player_control_obj = new PlayerControl(player_param);

    
    self.attr.match_play_item_col = new MatchPlayItemCol();

    self.attr.match_play_item_list = [];
    // self.attr.meet_obj = new RobinMeet({controler_obj:self});

    

    self.attr.match_play_view = new MatchPlayView({ controler_obj: self });
    // TODO

    self.attr.match_pool_view = new MatchPoolView({ controler_obj: self });
    // self.attr.view_obj = new RobinView();
    // self.attr.view_obj = new RobinView({controler_obj:this});
  }

  // RobinControlHelper.gen_match_pool(this, param_obj)
  static gen_match_pool(self, param_obj) {
    console.log('RobinControlHelper.gen_match_pool()')
    console.log(JSON.stringify(param_obj))
    const match_param = Object.assign({}, param_obj, {
      player_item_col: self.attr.player_item_col,
    });
    const match_pool_obj = new MatchPool();
    const pool_match_attr_item_list =
      match_pool_obj.create_pool_match_multi(match_param);

    const pool_match_item_list =   match_pool_obj.get_match_equal_item_list()
    
    console.log(JSON.stringify(pool_match_item_list[0]))
    // console.log(JSON.stringify(pool_match_attr_item_list))
    

    const view_param = {
      el_id: "el_match_pool_group",
      match_item_list: pool_match_item_list,
      controler_obj: self,
    };
    self.attr.match_pool_view.show(view_param);

    // TODO
  }

  // RobinControlHelper.start_match_play(this, param_obj)
  static start_match_play(self, match_item) {
    console.log('start_match_play()')

    // console.log(JSON.stringify(match_item))
    self.attr.match_play_item_col.add_item(match_item)
    // self.attr.match_play_item_col.add_item_with_attr(match_item)


    const param_obj = {el_id: "el_match_play_group", match_item_list: self.attr.match_play_item_col.get_main_item_list() }

    self.attr.match_play_view.show(param_obj) 
    // TODO

  }

  // RobinControlHelper.start_match_play(this, param_obj)
  static start_match_play_tbd(self, match_item) {
    console.log('start_match_play()')
    console.log(JSON.stringify(match_item))
    self.attr.match_play_item_list.push(match_item)
    const param_obj = {el_id: "el_match_play_group", match_item_list: self.attr.match_play_item_list}

    self.attr.match_play_view.show(param_obj) 
    // TODO

  }
}

class RobinControl {
  constructor(param_obj = {}) {
    RobinControlHelper.init(this, param_obj);
  }

  start(param_obj) {
    console.log("RobinControl.start()");
    const player_param = Object.assign({ el_id: "el_player_list" }, param_obj);
    this.attr.player_control_obj.start(player_param);
  }

  gen_match_pool(param_obj = {}) {
    RobinControlHelper.gen_match_pool(this, param_obj);
  }

  start_match_play(match_item = {}) {
    RobinControlHelper.start_match_play(this, match_item)
    // TODO

  }
}

module.exports = {
  RobinControl: RobinControl,
};
