const FastestValidator = require("fastest-validator");
const nunjucks = require("nunjucks");
const ClassHelper = require("./share-code.js").ClassHelper;
const ItemBase = require("./share-code.js").ItemBase;
const ItemListBase = require("./share-code.js").ItemListBase;

//// ParamUtil
class ParamUtil {
  // ParamUtil.is_valid(schema_key, data_obj)
  static is_valid(schema_key, data_obj) {
    const schema_obj = ParamUtil[schema_key];
    const validator_obj = new FastestValidator();
    const check_obj = validator_obj.compile(schema_obj);
    return check_obj(data_obj);
  }

  static control_init = {
    player_item_col: { type: "object" },
    controler_obj: { type: "object" },
  };

  static control_start = {
    player_item_col: { type: "object" },
    controler_obj: { type: "object" },
    el_id: { type: "string" },
  };
}

