//// Model
class PlayerItemHelper {
  // PlayerItemHelper.init(this, param_obj)
  static init(self, param_obj) {
    ClassHelper.init_attr(self, param_obj);
  }
}

class PlayerItemColHelper {
  // PlayerItemColHelper.init(this, param_obj)
  static init(self, param_obj) {
    ClassHelper.init_attr(self, param_obj);
    self.attr.main_item_list = [];
    PlayerItemColHelper.retrive_player_list(self, param_obj);
  }

  // PlayerItemListHelper.add_item(self, player_obj = {})
  static add_item(self, player_obj) {
    const player_item = new PlayerItem(player_obj);
    self.attr.main_item_list.push(player_item);
  }

  // PlayerItemColHelper.filter_by_id_list(this, id_list)
  static filter_by_id_list(self, id_list) {
    const item_list = [];
    for (let _id of id_list) {
      const item = _.find(self.attr.main_item_list, (item) => {
        return _id == item.get_attr("id");
      });
      if (item) {
        item_list.push(item.attr);
      }
    }
  
    return item_list;
    }

  // PlayerItemColHelper.filter_by_id_list(this, id_list)
  static filter_by_id_list_attr(self, id_list) {
    const item_list = [];
    for (let _id of id_list) {
      const item = _.find(self.attr.main_item_list, (item) => {
        return _id == item.get_attr("id");
      });
      if (item) {
        item_list.push(item);
      }
    }
    
    return item_list;
  }

  // PlayerItemColHelper.retrive_player_list(self, param_obj = {})
  static retrive_player_list(self, param_obj = {}) {
    const player_list = PlayerItemColHelper._read_player_list_json(param_obj);
    for (let player_obj of player_list) {
      PlayerItemColHelper.add_item(self, player_obj);
    }
  }

  // PlayerItemColHelper._read_player_list_json(param_obj = {})
  static _read_player_list_json(param_obj = {}) {
    const player_item_list = require("./dupr-player-list.js").player_item_list;
    for (let player of player_item_list) {
      player.is_selected = false;
    }

    return player_item_list;
  }
}

class PlayerItem extends ItemBase {
  constructor(param_obj = {}) {
    super(param_obj);
    PlayerItemHelper.init(this, param_obj);
  }
}

class PlayerItemCol extends ItemListBase {
  constructor(param_obj = {}) {
    super(param_obj);
    PlayerItemColHelper.init(this, param_obj);
  }

  add_player(player_item) {
    this.attr.main_item_list.push(player_item);
  }

  filter_by_id_list(id_list) {
    return PlayerItemColHelper.filter_by_id_list(this, id_list);
  }
}

//// View
class PlayerViewEvent {
  static on_click_checkbox(self, param_obj) {
    var el_list = document.getElementsByClassName("el_player");

    for (var i = 0; i < el_list.length; i++) {
      el_list[i].addEventListener("click", _on_click_checkbox, false);
    }

    function _on_click_checkbox() {
      var index_value = this.getAttribute("data-index");
      var value_attr = this.getAttribute("value");
      var check_value = this.checked;
    }
  }

  static on_click_select_all(self, param_obj) {
    var el_list = document.getElementsByClassName("el_player");

    document.getElementById("el_player_select_all").onclick = function () {
      for (var i = 0; i < el_list.length; i++) {
        el_list[i].checked = true;
      }
    };
  }

  static on_click_clear_all(self, param_obj) {
    var el_list = document.getElementsByClassName("el_player");

    document.getElementById("el_player_clear_all").onclick = function () {
      for (var i = 0; i < el_list.length; i++) {
        el_list[i].checked = false;
      }
    };
  }

  static on_click_gen_match(self, param_obj) {
    var el_list = document.getElementsByClassName("el_player");

    document.getElementById("el_player_gen_match").onclick = function () {
      const count = document.getElementById("el_player_gen_match_count").value;

      const player_id_list = [];
      for (var i = 0; i < el_list.length; i++) {
        if (el_list[i].checked) {
          const player_id = Number(el_list[i].value);
          player_id_list.push(player_id);
        }
      }
      if (player_id_list.length < 4) {
        alert(`Plese select at least 4 players. You selected ${player_id_list.length} players.`)
      } else {
        const out_param = {
          player_id_list: player_id_list,
          count: Number(count),
        };
        self.attr.controler_obj.gen_match_pool(out_param);
      }
    };
  }
}

class PlayerViewHelper {
  // PlayerViewHelper.init(this, param_obj)
  static init(self, param_obj) {
    ClassHelper.init_attr(self, param_obj);
  }

  // PlayerViewHelper.show_player(this, param_obj)
  static show_player(self, param_obj = {}) {
    const player_item_list = self.attr.player_item_col.filter_by_id_list(
      param_obj.player_id_list
    );
    const tmpl_str = PlayerViewTmpl.njk_bt5();
    const tmpl_data = { player_item_list: player_item_list };
    const html_str = nunjucks.renderString(tmpl_str, tmpl_data);
    const html_clean = beautyfy_html(html_str);
    const el_obj = document.getElementById(param_obj.el_id);
    el_obj.innerHTML = html_clean;
  }

  // PlayerViewHelper.show_player_button(this, param_obj)
  static show_player_button(self, param_obj = {}) {
    const el_id = "el_player_button";
    const tmpl_str = PlayerButtonViewTmpl.njk_bt5();

    const html_str = nunjucks.renderString(tmpl_str, {});
    const html_clean = beautyfy_html(html_str);
    const el_obj = document.getElementById(el_id);
    el_obj.innerHTML = html_clean;
    PlayerViewEvent.on_click_checkbox(self, param_obj);
    PlayerViewEvent.on_click_select_all(self, param_obj);
    PlayerViewEvent.on_click_clear_all(self, param_obj);
    PlayerViewEvent.on_click_gen_match(self, param_obj);
  }
}

class PlayerView {
  constructor(param_obj = {}) {
    PlayerViewHelper.init(this, param_obj);
  }

  show(param_obj = {}) {
    PlayerViewHelper.show_player(this, param_obj);
    PlayerViewHelper.show_player_button(this, param_obj);
  }
}

//// Control
class PlayerControlHelper {
  // PlayerControlHelper.init(this, param_obj)
  static init(self, param_obj) {
    const is_valid = ParamUtil.is_valid("control_init", param_obj);

    if (is_valid) {
      ClassHelper.init_attr(self, param_obj);

      const view_param = Object.assign(
        {},
        _.pick(self.attr, ["controler_obj", "el_id"]),
        { player_item_col: self.attr.player_item_col }
      );
      self.attr.player_view_obj = new PlayerView(view_param);
    } else {
      console.error("Error PlayerControlHelper.init()");
    }
  }
}

class PlayerControl {
  constructor(param_obj = {}) {
    PlayerControlHelper.init(this, param_obj);
  }

  start(param_obj) {
    this.attr.player_view_obj.show(param_obj);
  }
}

////
module.exports = {
  PlayerItemCol: PlayerItemCol,
  PlayerView: PlayerView,
  PlayerControl: PlayerControl,
};
