const _ = require("lodash");
const beautyfy_html = require("js-beautify").html;
const FastestValidator = require("fastest-validator");
const nunjucks = require("nunjucks");
const ClassHelper = require("./share-code.js").ClassHelper;

//// ParamUtil
class ParamUtil {
  // ParamUtil.is_valid(schema_key, data_obj)
  static is_valid(schema_key, data_obj) {
    const schema_obj = ParamUtil[schema_key];
    const validator_obj = new FastestValidator();
    const check_obj = validator_obj.compile(schema_obj);
    return check_obj(data_obj);
  }

  static control_init = {
    player_item_col: { type: "object" },
    controler_obj: { type: "object" },
  };

  static show = {
    match_item_list: { type: "object" },
    controler_obj: { type: "object" },
    el_id: { type: "string" },
  };
}

class MatchPoolViewTmpl {
  // MatchPoolViewTmpl.init(this, param_obj)
  static njk_bt5() {
    const tmpl_str = `
    {% macro show_player(player) %}
    <span>{{ player.first_name }} {{ player.last_name }}</span>
  {% endmacro %}
  
  {% macro show_player_list(player_item_list) %}
  
    <td>
    {% for player in player_item_list %}
      {{ show_player(player) }}
             {% if (loop.index -1 % 2) == 0 %}
             <div>&</div>
         {% endif %}
    {% endfor %}
    </td>
  
  {% endmacro %}
  
  {% macro show_team(team_item_list) %}
    {% for team in team_item_list %}
         {{ show_player_list(team.player_item_list) }}
         {% if (loop.index -1 % 2) == 0 %}
        <td>vs</td>
         {% endif %}
    {% endfor %}
  
  {% endmacro %}
  
  {% macro show_match_list(match_item_list) %}
  <div class="w3-container w3-panel">
  
      
    {% for match_obj in match_item_list %}
    <div class="w3-responsive">
    <table class='w3-table-all w3-centered'><tr>
      <td>{{loop.index}}</td>
      <td><button class="w3-button w3-green el_match_pool_start" data-index='{{loop.index}}'>Start</button></td>
      {{ show_team(match_obj.team_item_list) }}
      </tr></table>         
    </div>    
    {% endfor %}
  </div>
  {% endmacro %}
  
  <h4 class='w3-text-blue'>Potential Matches</h4>
  {{ show_match_list(match_item_list) }}
  
  
    
    `;
    return tmpl_str;
    // TODO
  }
}

class MatchPoolViewEvent {
  // MatchPoolViewEvent.on_click_start(this, param_obj)
  static on_click_start(self, param_obj) {
    var el_list = document.getElementsByClassName("el_match_pool_start");

    for (var i = 0; i < el_list.length; i++) {
      el_list[i].addEventListener("click", _on_click_checkbox, false);
    }

    function _on_click_checkbox() {
      var index_value = this.getAttribute("data-index");
      param_obj.selected_index = index_value-1
      MatchPoolViewHelper.start_match(self, param_obj)
    }    
    // TODO
  }
}

class MatchPoolViewHelper {
  // MatchPoolViewHelper.init(this, param_obj)
  static init(self, param_obj) {
    ClassHelper.init_attr(self, param_obj);
  }

  // MatchPoolViewHelper.show(this, param_obj)
  static show(self, param_obj = {}) {
    console.log("MatchPoolViewHelper.show()");
    console.log(param_obj);

    const tmpl_str = MatchPoolViewTmpl.njk_bt5();
    const tmpl_data = { match_item_list: param_obj.match_item_list };
    const html_str = nunjucks.renderString(tmpl_str, tmpl_data);
    const html_clean = beautyfy_html(html_str);
    const el_obj = document.getElementById(param_obj.el_id);
    el_obj.innerHTML = html_clean;
    MatchPoolViewEvent.on_click_start(self, param_obj)
    console.log(JSON.stringify(html_clean.length));
    // TODO
  }

  // MatchPoolViewHelper.start_match(this, param_obj)
  static start_match(self, param_obj) {
    const selected_index = param_obj.selected_index    
    console.log(selected_index)
    console.log(param_obj.match_item_list[selected_index])
    const pulled_item_list = _.pullAt(param_obj.match_item_list, [selected_index]);
    MatchPoolViewHelper.show(self, param_obj);
    self.attr.controler_obj.start_match_play(pulled_item_list[0]);
    // TODO
  }

}

class MatchPoolView {
  constructor(param_obj = {}) {
    MatchPoolViewHelper.init(this, param_obj);
  }

  show(param_obj) {
    MatchPoolViewHelper.show(this, param_obj);
  }
}

////
module.exports = {
  MatchPoolView: MatchPoolView,
};
